.work-timeline-block{display:flex;align-items:center;justify-content:space-evenly;min-height:100vh;width:99vw;margin:0;padding:2vh 100px;font-weight:300;color:#333;box-sizing:border-box}.work-timeline-block *{box-sizing:border-box}.work-timeline{width:100%;max-width:800px;background:rgba(40,30,103,.2784313725);padding:100px 50px;position:relative;box-shadow:.5rem .5rem 2rem 0 rgba(0,0,0,.2)}.work-timeline:before{content:"";position:absolute;top:0px;left:calc(33% + 15px);bottom:0px;width:4px;background:#ddd}.work-timeline:after{content:"";display:table;clear:both}.work-entry{clear:both;text-align:left;position:relative}.work-entry .work-title{margin-bottom:.5em;float:left;width:33%;padding-right:30px;text-align:right;position:relative}.work-entry .work-title:before{content:"";position:absolute;width:10px;height:10px;border:4px solid #5b8fb9;background-color:#fff;border-radius:100%;top:15%;right:-10px;z-index:99}.work-entry .work-title h3{margin:0}.work-entry .work-title p{margin:0;font-size:100%}.work-entry .work-body{margin:0 0 3em;float:right;width:66%;padding-left:30px}.work-entry .work-body p{font-size:1.5rem;line-height:1.4em}.work-entry .work-body p:first-child{margin-top:0;font-weight:650}.work-entry .work-body ul{color:#fff;padding-left:0;font-size:1.1rem;list-style-type:none}.work-entry .work-body ul li:before{content:"â€“";margin-right:.5em}.work-exp{text-align:center;font-size:2rem;padding-top:.5rem;padding-bottom:0}@media screen and (max-width: 640px){.work-timeline-block{width:100%;padding:0}}