.footer{
    width: 100%;
    padding: 6rem 0rem;
    background-color: rgba(19, 19, 19, 0.8);
}

.footer-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 10rem;
}

.left{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.location{
    display: flex;
    margin-bottom: 0.8rem;
}
.location p{
    line-height: 30px;
}

.right{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 0 1rem;
    color: white;
}

.social{
    margin-top: 1rem;
}
h4{
    font-size: 1.2rem;
    padding-bottom: 0.7rem;
}
p{
    font-size: 1.2rem;
}
.profile-title{
    padding-top: 2.0rem;
    padding-bottom: 0.7rem;
    font-weight: 700;
    font-size: 1rem;
}

.profile-icons{
    padding-bottom: 0.7rem;
}

@media screen and (max-width: 640px) {
    .footer-container{
        grid-template-columns:1fr;
    }
}